let changelook=['所有人可见','仅自己可见']
	import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue";
	import uploudImages from "../../components/common/uploud-images.vue";
	import uniPopup from "../../components/uni-popup/uni-popup.vue";
	
export default {
	data() {
		return {
			isget:false,
			showpopup:true,
			yinsi:"所有人可见",
			text:"1111",
			imglist:[]
		}
	},
	onBackPress() {
		// 如果有值
		if(!this.text && this.imglist.length<1){
			return ;
		}
		if(!this.isget){
			this.caogao();
			// 只有在该函数中返回值为 true 时，才表示不执行默认的返回，自行处理此时的业务逻辑。
			return true
		}
		
		
	},
	components:{
		uniNavBar,
		uploudImages,
		uniPopup
	},
	methods: {
		// 保存为草稿
		caogao(){
			uni.showModal({
				content:'是否保存为草稿',
				cancelText:'不保存',
				confirmText:'保存',
				success: (res) => {
					if(res.confirm){
						console.log("v")
						
					}else{
						
					}
					this.isget=true;
					uni.navigateBack({
						delta:1
					});
				},
			});
		},
		// 返回
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		// 发布
		submit(){
			console.log("发布")
		},
		// 隐私
		changelook(){
			console.log("隐私")
			uni.showActionSheet({
				itemList:changelook,
				success: (res) => {
					this.yinsi = changelook[res.tapIndex]
				}
			})
		},
		uploud(arr){
			this.imglist=arr
		},
		hidePopup(){
			this.showpopup = false
		}
		
		
	},
	onNavigationBarButtonTap(e){
		// index:0 取消按钮
		if(e.index == 0){
			uni.navigateBack({
				// 向上返回一级
				delta:1
			});
		}
		
	},
}
